$bgcolor: #282c34;
$color-primary-0: #FF0000;	// Main Primary color */
$color-primary-1: #FF6464;
$color-primary-2: #FF3939;
$color-primary-3: #C60000;
$color-primary-4: #9B0000;

$color-secondary-1-0: #FFD300;	// Main Secondary color (1) */
$color-secondary-1-1: #FFE464;
$color-secondary-1-2: #FFDD39;
$color-secondary-1-3: #C6A400;
$color-secondary-1-4: #9B8100;

$color-secondary-2-0: #3914AF;	// Main Secondary color (2) */
$color-secondary-2-1: #735AC4;
$color-secondary-2-2: #5639B5;
$color-secondary-2-3: #2B0F88;
$color-secondary-2-4: #20096B;

$color-complement-0: #00CC00;	// Main Complement color */
$color-complement-1: #55D955;
$color-complement-2: #2FCF2F;
$color-complement-3: #009E00;
$color-complement-4: #007C00;
* {
  //border: 1px solid red;
}
header, footer {
  background-color: $bgcolor;
  color: white;
}

header {
  min-height: 100vh;
  font-size: calc(10px + 3vmin);
  display: grid;
  grid-template-rows: 20vh auto 40vh;
  width: 100vw;
}

header > a {
  justify-self: end;
  background: url("github.png");
}

.icon {
  width: 32px;
  height: 32px;
  margin: 20px;
}

header .heading {
  justify-self: center;
  text-align: center;
}

header > div {
  justify-self: stretch;
}

.info > div {
  display: grid
}


.box.c {
background: $color-secondary-2-2;
}
.box.d {
  background: $color-primary-2
}
.box.r{
  background: $color-complement-0;
}
.f{
  display: grid;
  grid-template-columns: 20% 60% 20%;
}

.bat {
  height: 15vh;
  display: block;
}
footer {
  font-size: calc(8px + 1vmin);
  min-height: 20vh;
}

.info {
  display: grid;
  grid-template-columns: 25vw 25vw 25vw 25vw;
  text-align: center;
  font-size: calc(8px + 1vmin);

}

.info > * {
  background: #323a46;
  margin: 20px;
  padding: 20px;
}

.info > div > svg {
  display: block;
  width: 100px;
  height: 100px;
  margin: auto;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

